export default {
	data() {
		return {
			PageCur: '我的',
			modalNameS1: null,
			modalNameS2: null,
		};
	},
	
	methods: {
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		showModalS1(e) {
			this.modalNameS1 = e.currentTarget.dataset.target
		},
		hideModalS1(e) {
			this.modalNameS1 = null
		},
		showModalS2(e) {
			this.modalNameS2 = e.currentTarget.dataset.target
		},
		hideModalS2(e) {
			this.modalNameS2 = null
		},
	}
}
